t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMessage/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> // .</span>
        </div>
        <table>
            <tr>
                <th width="5%"></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th width="10%"></th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.charging }}</td>
                <td>{{ q.bonus }}</td>
                <td>{{ q.firstbunus }}</td>
                <td>{{ q.mileage }}</td>
                <td>
                    <button @click="hyper('ChargingSetting')"></button>
                    <button @click="del(index)"></button>
                </td>
            </tr>
        </table>
        <div class="tableBottom">
            <div class="tableBottomLeft">
                <button @click="hyper('ChargingSetting')"></button>
            </div>
        </div>
        <div class="tableBottomPaging">
            <div class="tableBottomCenter">
              <img src="../../assets/btn_prev2.gif">
              <img src="../../assets/btn_prev.gif">
              <p>1/</p>
              <img src="../../assets/btn_next.gif">
              <img src="../../assets/btn_next2.gif">
            </div>
          </div>
        <div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMessage from '../SideMenuMessage'
import Header from '../Header'

export default {
  name: 'ChargingList',
  components: {
    SideMenuMessage,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          charging: '1',
          bonus: '0',
          firstbunus: '0',
          mileage: '0'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table {
    text-align: center;
}
.tableBottom {
  margin-top: 10px;
}
</style>



