t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuInfo/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> // .</span>
        </div>
        <table>
            <tr>
                <th>*</th>
                <td>
                    <input type="text" value="admin">
                </td>
                <th>*</th>
                <td>
                    <input type="password">
                </td>
            </tr>
            <tr>
                <th>*</th>
                <td>
                    <input type="text" value="">
                </td>
                <th>*</th>
                <td>
                    <input type="text" value="dd.dd@dd.com">
                </td>
            </tr>
            <tr>
                <th></th>
                <td class="residentNumber">
                    <input type="text"> - <input type="text">
                </td>
                <th></th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <input type="text">
                </td>
                <th></th>
                <td></td>
            </tr>
            <tr class="Mail">
                <th></th>
                <td>
                    <input type="text"> - <input type="text">
                    <button @click="mail"> </button>
                </td>
            </tr>
            <tr>
                <th></th>
                <td class="address">
                    <input type="text"><br>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>  IP </th>
                <td>
                    <div class="radio__items">
                     <input type="radio" name="yesno"><span></span><input type="radio" name="yesno" checked>
                    </div>
                </td>
            </tr>
            <tr>
                <th>  IP</th>
                <td>
                    <textarea cols="80" rows="5"></textarea>
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <textarea cols="80" rows="5"></textarea>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('adminlist')"></button>
                <button @click="hyper('adminlist')"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuInfo from '../SideMenuInfo'
import Header from '../Header'

export default {
  name: 'AdminList',
  components: {
    SideMenuInfo,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td input {
    width: 50px;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.residentNumber > input {
    width: 83px;
}
.address > input {
    width: 400px;
}
</style>



