t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span>  .</span>
        </div>
        <table class="search">
          <tr>
            <th class="searchTh"></th>
            <td>
              <select>
                <option></option>
                <option></option>
              </select>
              <input type="text">
              <button></button>
            </td>
          </tr>
        </table>
        <table>
            <tr>
                <th width="5%"></th>
                <th width="10%"></th>
                <th></th>
                <th></th>
                <th width="10%"></th>
                <th width="10%"></th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.name }}</td>
                <td>{{ q.title }}</td>
                <td>{{ q.info }}</td>
                <td>{{ q.day }}</td>
                <td><button @click="del(index)"></button></td>
            </tr>
        </table>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'OutMember',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          name: '',
          title: '',
          info: '',
          day: '2021-06-15 15:00:20'
        },
        {
          number: '2',
          name: '',
          title: '  ',
          info: '',
          day: '2021-06-15 15:00:20'
        },
        {
          number: '3',
          name: '',
          title: '  ',
          info: '',
          day: '2021-06-15 15:00:20'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.searchTh {
  text-align: left;
}
table {
  text-align: center;
}
.search {
  margin-bottom: 10px;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



