t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> / .</span>
        </div>
        <table class="search">
          <tr>
            <th class="searchTh"></th>
            <td>
              <select>
                <option>::::</option>
                <option></option>
                <option></option>
              </select>
              <select>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option>  ip</option>
                <option>  ip</option>
              </select>
              <input type="text">
              <button></button>
            </td>
          </tr>
        </table>
        <div class="tableTop">
          <div class="tabelTopLeft"><span>  : 1,   : 1</span></div>
          <div class="tabelTopRight">
            <button></button>
            <button @click="hyper('MemberAdd')"></button>
          </div>
        </div>
        <table>
            <tr>
                <th width="5%"><input type="checkbox"></th>
                <th width="5%"></th>
                <th width="10%"></th>
                <th width="10%"></th>
                <th width="10%"></th>
                <th width="5%">SMS</th>
                <th width="5%">LMS</th>
                <th width="5%">MMS</th>
                <th width="5%"></th>
                <th width="5%"></th>
                <th width="10%"></th>
                <th>  IP</th>
                <th width="5%"></th>
                <th width="10%"></th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td><input type="checkbox"></td>
                <td>{{ q.number }}</td>
                <td>{{ q.name }}</td>
                <td>{{ q.id }}</td>
                <td>{{ q.phone }}</td>
                <td>{{ q.sms }}</td>
                <td>{{ q.lms }}</td>
                <td>{{ q.mms }}</td>
                <td>{{ q.cash }}p</td>
                <td>{{ q.visit }}</td>
                <td>{{ q.join }}</td>
                <td>{{ q.ip }}</td>
                <td>{{ q.approval }}</td>
                <td>
                    <button @click="hyper('MemberView')"></button>
                </td>
            </tr>
        </table>
        <div class="tableBottom">
          <div class="tableBottomLeft">
              <button @click="hyper('')"></button>
              <button @click="sms">SMS</button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'MemberList',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          name: '',
          id: 'angelofsky',
          phone: '010-1122-1122',
          sms: '10',
          lms: '5',
          mms: '2',
          cash: '11.225',
          visit: '250',
          join: '2021-06-15',
          ip: '',
          approval: 'Y'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    sms () {
      window.open('/#/smsPopup', 'a', 'width=510, height=370, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.searchTh {
     text-align: left;
}
.tableTop {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 5px;
}
.tabelTopLeft > span {
  font-size: 13px;
  font-weight: bold;
}
table {
    text-align: center;
}
.tableBottom {
    margin-top: 10px;
    display: flex;
    width: 100%;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



