t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuInfo/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> // .</span>
        </div>
        <table>
            <tr>
                <th>*</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>*</th>
                <td>
                    <input type="text"> ~ <input type="text">
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <div class="radio__items">
                    <input type="radio" name="nomal" checked><span></span><input type="radio" name="nomal">
                    </div>
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <div class="radio__items">
                       <input type="radio" name="yesno" checked><span></span><input type="radio" name="yesno">
                    </div>
                </td>
                <th></th>
                <td>
                    <div class="radio__items">
                       <input type="radio" name="yesno"><span></span><input type="radio" name="yesno" checked>
                    </div>
                </td>
            </tr>
            <tr>
                <th>*</th>
                <td>
                    X : <input type="text" value="100">
                    Y : <input type="text" value="100">
                </td>
                <th></th>
                <td>
                     : <input type="text" value="340">
                     : <input type="text" value="400">
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <Editor/>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('popuplist')"></button>
                <button @click="hyper('popuplist')"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuInfo from '../SideMenuInfo'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'PopupAdmin',
  components: {
    SideMenuInfo,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td input {
    width: 50px;
    margin-right: 10px;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.address > input {
    width: 400px;
}
</style>



