t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPost/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span>  .</span>
        </div>
        <table>
            <tr>
                <th></th>
                <td class="td__size">
                    <input type="text">
                </td>
                <th></th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th></th>
                <td class="td__size">
                    <input type="text">
                </td>
                <th></th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th rowspan="2"></th>
                <td colspan="3">
                    <input type="checkbox">
                    <span></span>
                    <input type="checkbox">
                    <span></span>
                    <input type="checkbox">
                    <span>HTML</span>
                </td>
            </tr>
            <tr>
                <td colspan="3">
                    <input type="text" class="title__input">
                </td>
            </tr>
            <tr>
                <th></th>
                <td colspan="3">
                    <Editor/>
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>1</th>
                <td>
                    <input type="file">
                </td>
            </tr>
            <tr>
                <th>2</th>
                <td>
                    <input type="file">
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button></button>
                <button @click="cancle"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuPost from '../SideMenuPost'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'PostAdd',
  components: {
    SideMenuPost,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    cancle () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.td__size {
    width: 500px;
}
.title__input {
    width: 50%
}
</style>



