<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPost/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> /,  .</span>
        </div>
        <div class="section__top">
            <div class="section__top__left">
                <span> :</span>
                <select v-model="list" @change="change($event)">
                    <option value="notice"></option>
                    <option value="qna">1:1</option>
                    <option value="taq"></option>
                </select>
            </div>
            <div class="section__top__right">
                <select>
                    <option value="title"></option>
                    <option value="content"></option>
                </select>
                <input type="text">
                <button></button>
            </div>
        </div>
        <table>
            <tr>
              <th width="5%"><input type="checkbox"></th>
              <th width="5%"></th>
              <th></th>
              <th width="10%"></th>
              <th width="10%"></th>
              <th width="5%"></th>
              <th width="10%"></th>
            </tr>
            <tr>
              <td colspan="5">  .</td>
            </tr>
        </table>
        <div class="section__bottom">
          <div>
            <button @click="add"></button>
            <button></button>
          </div>
          <div>
            <button></button>
            <button></button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuPost from '../SideMenuPost'

export default {
  name: 'Taq',
  components: {
    Header,
    SideMenuPost
  },
  data () {
    return {
      list: 'taq'
    }
  },
  watch: {
  },
  methods: {
    change (event) {
      this.$router.push(event.target.value)
    },
    add () {
      this.$router.push('PostAdd')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
    display: flex;
    justify-content: space-between;
}
.section__top__left > span {
    font-size: 13px;
    font-weight: bold;
}
table {
    margin-top: 10px;
    text-align: center;
}
.section__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
</style>



