<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuBanner/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> // .</span>
        </div>
        <div class="section__top">
          <table>
            <tr>
              <th class="th__size">()</th>
              <td class="td__size">
                <input type="text">
              </td>
              <th class="th__size"></th>
              <td class="td__size">
                <input type="text">
              </td>
            </tr>
            <tr>
              <th class="th__size"></th>
              <td class="td__size">
                <div class="radio__items">
                  <input type="radio" name="type">
                  <span></span>
                  <input type="radio" name="type">
                  
                </div>
              </td>
              <th class="th__size"></th>
              <td class="td__size">
                <input type="text">
              </td>
            </tr>
            <tr>
              <th class="th__size"></th>
              <td class="td__size">
                <div class="radio__items">
                  <input type="radio" name="use">
                  <span></span>
                  <input type="radio" name="use">
                  
                </div>
              </td>
              <th class="th__size"></th>
              <td class="td__size">
                <select>
                  <option value="">1</option>
                  <option value="">2</option>
                  <option value="">3</option>
                  <option value="">4</option>
                  <option value="">5</option>
                  <option value="">6</option>
                  <option value="">7</option>
                  <option value="">8</option>
                  <option value="">9</option>
                </select>
              </td>
            </tr>
          </table>
        </div>
        <div class="button__section">
            <div>
                <button @click="check"></button>
                <button @click="back"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuBanner from '../SideMenuBanner'

export default {
  name: 'BannerMain',
  components: {
    Header,
    SideMenuBanner
  },
  data () {
    return {
    }
  },
  watch: {
  },
  methods: {
    check () {
      this.$router.push('BannerMain')
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
    display: flex;
    justify-content: space-between;
}
.th__size {
    width: 200px;
}
.td__size {
    text-align: left;
}
</style>



