<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuStatistics/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span> //  .</span>
        </div>
        <div class="section__top">
          <table>
            <tr>
              <th class="th__size"></th>
              <td @change="change" class="td__size">
                <input type="radio" name="qq" id="0" value="0" v-model="check"><label for="0"></label>
                <input type="radio" name="qq" id="1" value="1" v-model="check"><label for="1"></label>
                <input type="radio" name="qq" id="2" value="2" v-model="check"><label for="2"></label>
              </td>
            </tr>
            <tr>
              <th class="th__size"></th>
              <td class="td__size">
                <select name="" id="">
                  <option value="">2019</option>
                  <option value="">2020</option>
                  <option value="">2021</option>
                </select>
                <span></span>
                <select name="" id="">
                  <option v-for="m in 12" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span></span>
                <select name="" id="">
                <option v-for="m in 31" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span> ~</span>
                <select name="" id="">
                  <option value="">2019</option>
                  <option value="">2020</option>
                  <option value="">2021</option>
                </select>
                <span></span>
                <select name="" id="">
                  <option v-for="m in 12" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span></span>
                <select name="" id="">
                <option v-for="m in 31" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span></span>
                <button></button>
                <button> </button>
              </td>
            </tr>
          </table>
        </div>
        <div class="section__middle">
          <div>
            <GChart
              type="ColumnChart"
              :data="chartData"
              :options="chartOptions"
              value="test"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuStatistics from '../SideMenuStatistics'
import { GChart } from 'vue-google-charts'

export default {
  name: 'Statistics',
  components: {
    Header,
    SideMenuStatistics,
    GChart
  },
  data () {
    return {
      check: '0',
      chartData: [
        ['', ''],
        ['1', 165],
        ['2', 135],
        ['3', 157],
        ['4', 139],
        ['5', 136],
        ['6', 135],
        ['7', 157],
        ['8', 139],
        ['9', 136],
        ['10', 135],
        ['11', 157],
        ['12', 139],
        ['13', 136],
        ['14', 135],
        ['15', 157],
        ['16', 139],
        ['17', 136],
        ['18', 135],
        ['19', 157],
        ['20', 139],
        ['21', 136],
        ['22', 135],
        ['23', 157],
        ['24', 139],
        ['25', 136],
        ['26', 136],
        ['27', 135],
        ['28', 157],
        ['29', 139],
        ['30', 136],
        ['31', 136]
      ],
      chartOptions: {
        chart: {
          title: 'Company Performance',
          subtitle: 'Sales, Expenses, and Profit: 2014-2017'
        }
      }
    }
  },
  methods: {
    change () {
      const rowData = [
        ['', ''],
        ['1', 165],
        ['2', 135],
        ['3', 157],
        ['4', 139],
        ['5', 136],
        ['6', 135],
        ['7', 157],
        ['8', 139],
        ['9', 136],
        ['10', 135],
        ['11', 157],
        ['12', 139],
        ['13', 136],
        ['14', 135],
        ['15', 157],
        ['16', 139],
        ['17', 136],
        ['18', 135],
        ['19', 157],
        ['20', 139],
        ['21', 136],
        ['22', 135],
        ['23', 157],
        ['24', 139],
        ['25', 136],
        ['26', 136],
        ['27', 135],
        ['28', 157],
        ['29', 139],
        ['30', 136],
        ['31', 136]
      ]

      const rowDataB = [
        ['', ''],
        ['1', 165],
        ['2', 135],
        ['3', 157],
        ['4', 139],
        ['5', 136],
        ['6', 135],
        ['7', 157],
        ['8', 139],
        ['9', 136],
        ['10', 135],
        ['11', 136],
        ['12', 135]
      ]

      const rowDataC = [
        ['', ''],
        ['2018', 165],
        ['2019', 135],
        ['2020', 157],
        ['2021', 139]
      ]

      switch (this.check) {
        case '0':
          this.chartData = rowData
          break
        case '1':
          this.chartData = rowDataB
          break
        case '2':
          this.chartData = rowDataC
          break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
  display: flex;
  justify-content: space-between;
}
.section__top__left > span {
  font-size: 13px;
  font-weight: bold;
}
.section__middle {
  margin-top: 15px
}
.seciton__middle__table {
  margin-top: 5px;
  text-align: center;
}
.th__size {
  width: auto;
}
.td__size > label {
  cursor: pointer;
}
</style>



