t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3></h3>
          <span>   .</span>
        </div>
        <table class="search">
          <tr>
            <th class="searchTh"></th>
            <td>
              <select>
                <option></option>
                <option></option>
                <option></option>
                <option> - </option>
                <option> - </option>
                <option> - </option>
                <option> - </option>
                <option></option>
                <option></option>
              </select>
              <select>
                <option></option>
                <option> </option>
              </select>
              <input type="text">
              <button></button>
            </td>
          </tr>
        </table>
        <div class="tableTop">
          <div class="tabelTopLeft"><span>  : 1,   : 1</span></div>
        </div>
        <table>
            <tr>
                <th width="5%"><input type="checkbox"></th>
                <th width="5%"></th>
                <th width="10%"></th>
                <th width="10%"></th>
                <th></th>
                <th width="20%"></th>
                <th width="10%"></th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td><input type="checkbox"></td>
                <td>{{ q.number }}</td>
                <td>{{ q.id }}</td>
                <td>{{ q.point }}</td>
                <td>{{ q.pointinfo }}</td>
                <td>{{ q.day }}</td>
                <td><button @click="del(index)"></button></td>
            </tr>
        </table>
        <div class="tableBottom">
          <div class="tableBottomLeft">
            <button></button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'PointList',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          id: 'angelofsky',
          point: '5,000',
          pointinfo: ' ',
          day: '2021-06-15 13:30:21'
        },
        {
          number: '2',
          id: 'sdwdq',
          point: '7,000',
          pointinfo: ' ',
          day: '2021-06-15 13:30:21'
        },
        {
          number: '3',
          id: 'azlzlzls',
          point: '121,000',
          pointinfo: ' ',
          day: '2021-06-15 13:30:21'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.searchTh {
  text-align: left;
}
.tableTop {
  width: 100%;
  height: 24px;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 5px;
}
.tabelTopLeft > span {
  font-size: 13px;
  font-weight: bold;
}
table {
  text-align: center;
}
.tableBottom {
  margin-top: 10px;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



